
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}






































































































































































































/* 整个元素 begin --------------------------------------------------------------- */
.todo_item[data-v-1878e278] {
  background-color: transparent;
  height: 70px;
  padding: 10px 25px 10px 25px;
  border-bottom: 1px solid rgb(201, 201, 201);

  max-width: 1000px;
  min-width: 550px;
}
/* 勾选项 begin --------------------------------------------------------------- */
.todo_check[data-v-1878e278] {
  width: 70px;
  height: 70px;
  position: relative;
  float: left;
}
.todo_btn_check[data-v-1878e278] {
  background-color: transparent;
  width: 70px;
  height: 70px;
}
/* 按钮样式 begin --------------------------------------------------------------- */
.todo_tools[data-v-1878e278] {
  height: 70px;
  float: right;
  position: relative;
  background-color: transparent;
  top: 9px;
  right: 0;
}
.todo_tools input[data-v-1878e278] {
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
}
/* 文本 begin --------------------------------------------------------------- */
.todo_text[data-v-1878e278] {
  position: relative;
  background-color: transparent;
  top: 10px;
  margin-left: 90px;
  margin-right: 100px;
}
.todo_text span[data-v-1878e278] {
  display: block;
  background-color: transparent;
  margin-left: 0;
  font-size: 38px;
  font-weight: bold;
  text-align: left;
}
.todo_text input[data-v-1878e278] {
  margin-left: 0;
  font-size: 38px;
  font-weight: bold;
  top: 50%;
  position: relative;
  float: left;
  width: 100%;
}
.todo_text .todo_text_new[data-v-1878e278] {
  color: rgb(211, 211, 211);
}
.todo_text .todo_text_normal[data-v-1878e278] {
  color: rgb(46, 46, 46);
}
.todo_text .todo_text_finish[data-v-1878e278] {
  color: rgb(160, 160, 160);
  font-style: italic;
  text-decoration: line-through;
}
.todo_text .todo_text_input[data-v-1878e278] {
  background-color: white;
  border: 0;
  margin-top: 3px;
  padding-left: 0px;
  color: rgb(19, 19, 59);
}



















































































